@import '../../styles/breakpoints.scss';

.about-me {
    width: 95vw;
    margin-right: auto;
    margin-left: auto;
    @include small {
        width: 90vw;
    }
    @include medium {
        width: 80vw;
    }
    @include large {
        width: 70vw;
    }
    @include extra-large {
        width: 60vw;
    }
}